@charset "UTF-8";

/**
 * Основные стили
 **/

html {
	font-size: 16px;
	color: $black;
}

body {
	font-family: 'Roboto', sans-serif;
	overflow-wrap: break-word;
	word-wrap: break-word;
	font-weight: 300;
}

.container {
	@include min-max(320px, 100%, 0);
}

.wrap {
	@include min-max(320px, 1170px, 15px);
}

h2 {
	font-size: px-to-rem(32px);
	font-weight: 400;
}