@charset "UTF-8";
/* Указываем box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внутренние отступы */
ul[class],
ol[class] {
  padding: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.3;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class],
ol[class] {
  list-style: none;
}

/* Упрощаем работу с изображениями */
img {
  max-width: 100%;
  display: block;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
  font: inherit;
}

/**
 * Основные стили
 **/
html {
  font-size: 16px;
  color: #000;
}

body {
  font-family: "Roboto", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
  font-weight: 300;
}

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto;
}

.wrap {
  min-width: 320px;
  max-width: 1170px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto;
}

h2 {
  font-size: 2rem;
  font-weight: 400;
}

.header__wrap {
  background-color: #FAFAFA;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1440px;
  margin: 0 auto;
  padding: 35px 25px;
}

.header__logo {
  display: block;
  width: 156px;
}

.header__nav {
  display: flex;
  font-weight: 400;
}
.header__nav li {
  margin: 5px 15px;
}
.header__nav a {
  text-decoration: none;
  color: #000;
  font-size: 1.125rem;
}
.header__nav a:hover {
  text-decoration: underline;
}

.header__finder {
  text-decoration: none;
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #F7941E;
  border-radius: 5px;
  padding: 15px 25px;
  font-size: 1.125rem;
  display: inline-block;
  border: 1px solid #F7941E;
  width: 156px;
  padding: 10px 0;
}
.header__finder:hover {
  border-color: #a95f06;
  color: #784304;
}

@media (max-width: 780px) {
  .header {
    flex-direction: column;
  }

  .header__logo {
    margin-bottom: 25px;
    width: auto;
  }

  .header__nav {
    margin-bottom: 25px;
    flex-wrap: wrap;
    justify-content: center;
  }
}
.about__wrap {
  background-color: #F1F1F2;
  max-width: 1440px;
  margin: 0 auto;
}

.about {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "find imageright" "imageleft work";
}
.about h1 {
  font-size: 3.5rem;
  font-weight: 400;
  line-height: 4.125rem;
}
.about p {
  font-size: 1.25rem;
  line-height: 1.75rem;
}

.about__find {
  grid-area: find;
  justify-content: center;
  align-items: center;
  display: grid;
}
.about__find div {
  max-width: 440px;
}
.about__find p {
  margin: 45px 0 52px 0;
}

.about__image-right {
  grid-area: imageright;
}

.about__image-left {
  grid-area: imageleft;
}

.btn {
  text-decoration: none;
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  border-radius: 5px;
  padding: 15px 25px;
  font-size: 1.125rem;
  display: inline-block;
  background-color: #F7941E;
  width: 200px;
}
.btn:hover {
  background-color: #a95f06;
}

.about__work {
  grid-area: work;
  justify-content: center;
  align-items: center;
  display: grid;
}
.about__work div {
  max-width: 440px;
}
.about__work .btn {
  text-decoration: none;
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #F7941E;
  border-radius: 5px;
  padding: 15px 25px;
  font-size: 1.125rem;
  display: inline-block;
  border: 1px solid #F7941E;
  background: rgba(0, 0, 0, 0);
}
.about__work .btn:hover {
  border-color: #a95f06;
  color: #784304;
}
.about__work p {
  margin: 25px 0 35px 0;
  text-align: justify;
}

.about__image {
  width: 100%;
  height: auto;
  object-fit: contain;
}

@media (max-width: 1000px) {
  .about {
    grid-template-columns: 100%;
    grid-template-areas: "imageright" "find" "imageleft" "work";
  }
  .about .btn {
    width: 160px;
    font-size: 0.9375rem;
  }

  .about__image {
    margin: 0 auto;
  }

  .about__work, .about__find {
    padding: 45px 20px;
  }
}
@media (max-width: 568px) {
  .about h1 {
    font-size: 2.1875rem;
    font-weight: 400;
    line-height: 2.8125rem;
  }
  .about h2 {
    font-size: 1.625rem;
    font-weight: 400;
  }
  .about p {
    font-size: 1rem;
    line-height: 1.625rem;
    margin: 25px 0 30px;
  }
}
.gifts__wrap {
  max-width: 1440px;
  margin: 0 auto;
  padding: 90px 0;
}

.section__title {
  text-align: center;
}
.section__title h2 {
  margin-bottom: 24px;
}
.section__title p {
  font-size: 1.125rem;
  margin-bottom: 64px;
}

.gifts__block {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.gifts__item {
  background-color: #FAFAFA;
  max-width: 270px;
}
.gifts__item:hover {
  background-color: #fff;
  box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05);
}

.gifts__item-txt {
  padding: 24px;
}
.gifts__item-txt h3 {
  font-size: 1.25rem;
  font-weight: 400;
  margin-bottom: 75px;
}
.gifts__item-txt a {
  font-size: 0.875rem;
  font-weight: 400;
  text-decoration: none;
  color: #000;
}
.gifts__item-txt a:hover {
  text-decoration: underline;
}

@media (max-width: 800px) {
  .gifts__block {
    flex-wrap: wrap;
    justify-content: space-evenly;
    row-gap: 20px;
  }
}
.singup_wrap {
  background-color: #F7941E;
  padding: 80px 0;
}

.singup {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.singup__text {
  font-size: 2rem;
}
.singup__text h2 {
  font-weight: 400;
}

.singup__btns {
  display: flex;
  gap: 15px;
}
.singup__btns .btn-black {
  text-decoration: none;
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  border-radius: 5px;
  padding: 15px 25px;
  font-size: 1.125rem;
  display: inline-block;
  background-color: #000;
}
.singup__btns .btn-black:hover {
  background-color: black;
}
.singup__btns .btn-black:hover {
  background-color: #a95f06;
}
.singup__btns .btn-border {
  text-decoration: none;
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #000;
  border-radius: 5px;
  padding: 15px 25px;
  font-size: 1.125rem;
  display: inline-block;
  border: 1px solid #000;
}
.singup__btns .btn-border:hover {
  border-color: black;
  color: black;
}
.singup__btns .btn-border:hover {
  background-color: rgba(0, 0, 0, 0);
  border-color: #a95f06;
  color: #784304;
}

@media (max-width: 900px) {
  .singup {
    flex-direction: column;
    justify-content: center;
  }

  .singup__text {
    margin-bottom: 35px;
  }
}
@media (max-width: 500px) {
  .singup__text {
    text-align: center;
    font-size: 1.625rem;
  }

  .singup__btns {
    flex-direction: column;
    justify-content: center;
  }
  .singup__btns .btn {
    width: 280px;
  }
}
.posts__wrap {
  padding: 80px 0;
}

.posts__block {
  display: grid;
  row-gap: 40px;
  column-gap: 30px;
  grid-template-areas: "postbig postbig" "postleft postright";
}

.posts__block-big {
  grid-area: postbig;
  display: flex;
  background-color: #231F20;
  color: #fff;
  align-items: center;
}

.posts-left {
  grid-area: postleft;
}

.posts-righ {
  grid-area: postright;
}

.posts__block-big-text {
  margin-left: 70px;
  max-width: 490px;
}
.posts__block-big-text h3 {
  font-size: 3rem;
  line-height: 3.5rem;
  font-weight: 400;
  margin: 27px 0 60px 0;
}
.posts__block-big-text .posts__block-big-author {
  font-size: 0.875rem;
}
.posts__block-big-text .posts__block-big-btn {
  font-size: 1.125rem;
  text-decoration: none;
  color: #fff;
}
.posts__block-big-text .posts__block-big-btn:hover {
  text-decoration: underline;
}

.posts__block-item {
  padding: 50px;
}
.posts__block-item h3 {
  font-size: 1.5rem;
  line-height: 2.0625rem;
  margin-bottom: 20px;
}
.posts__block-item p {
  font-size: 1.125rem;
  line-height: 1.5625rem;
  margin-bottom: 20px;
}
.posts__block-item a {
  text-decoration: none;
  font-size: 0.875rem;
  color: #000;
}
.posts__block-item a:hover {
  text-decoration: underline;
}
.posts__block-item:hover {
  box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05);
}

@media (max-width: 1024px) {
  .posts__block-big-text {
    margin-left: 40px;
    margin-right: 40px;
    max-width: 400px;
  }
  .posts__block-big-text h3 {
    font-size: 2.5rem;
    line-height: 3.125rem;
  }
}
@media (max-width: 800px) {
  .posts__block {
    row-gap: 20px;
    grid-template-areas: "postbig" "postleft" "postright";
  }

  .posts__block-big-text {
    margin-left: 20px;
    margin-right: 20px;
    max-width: 330px;
  }
  .posts__block-big-text h3 {
    font-size: 1.625rem;
    line-height: 2.0625rem;
    margin: 15px 0 30px 0;
  }
  .posts__block-big-text .posts__block-big-author {
    font-size: 0.8125rem;
  }
  .posts__block-big-text .posts__block-big-btn {
    font-size: 1rem;
  }
}
@media (max-width: 500px) {
  .posts__block-big {
    flex-direction: column;
  }

  .posts__block-big-img {
    width: 100%;
  }
  .posts__block-big-img img {
    width: 100%;
  }

  .posts__block-big-text {
    margin: 30px;
    max-width: 380px;
  }
  .posts__block-big-text .posts__block-big-author {
    font-size: 0.75rem;
  }
  .posts__block-big-text .posts__block-big-btn {
    font-size: 0.875rem;
  }
  .posts__block-big-text h3 {
    font-size: 1.5rem;
    line-height: 2.0625rem;
    margin: 15px 0 30px 0;
  }

  .posts__block-item {
    padding: 30px;
  }
  .posts__block-item p {
    font-size: 1rem;
    line-height: 1.375rem;
    margin-bottom: 20px;
  }
}
.footer__wrap {
  background-color: #FAFAFA;
  padding: 115px 0 140px 0;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 0.875rem;
  font-weight: 400;
}

.footer__nav {
  display: flex;
  justify-content: center;
}
.footer__nav li {
  margin: 10px 20px;
}
.footer__nav li a {
  text-decoration: none;
  color: #000;
}
.footer__nav li a:hover {
  text-decoration: underline;
}

.footer__hr {
  height: 1px;
  background-color: #000;
  width: 85%;
  margin: 50px 0 60px 0;
}

@media (max-width: 430px) {
  .footer__nav {
    flex-wrap: wrap;
    justify-content: center;
  }
}