.header__wrap {
    background-color: $bg_gray;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: $mw_container;
    margin: 0 auto;
    padding: 35px 25px;
}

.header__logo {
    display: block;
    width: 156px;
}

.header__nav {
    display: flex;
    font-weight: 400;

    li {
        margin: 5px 15px;  
    }

    a {
        text-decoration: none;
        color: $black;
        font-size: px-to-rem(18px);
        &:hover {
            text-decoration: underline;
        }   
    }
}

.header__finder {
    @include button();
    width: 156px;
    padding: 10px 0;

}

@media (max-width: 780px) {

    .header {
        flex-direction: column;
    }

    .header__logo {
        margin-bottom: 25px;
        width: auto;
    }

    .header__nav {
        margin-bottom: 25px;
        flex-wrap: wrap;
        justify-content: center;
    }
}
