.singup_wrap {
    background-color: $orange;
    padding: 80px 0;
}
.singup {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.singup__text {

    font-size: px-to-rem(32px);

    h2 {
        font-weight: 400;
    }

}
.singup__btns {

    display: flex;
    gap: 15px;

    .btn-black {
        @include button($black, #fff, false);
        &:hover {
            background-color:darken($orange, 20%);
        }
    }
    
    .btn-border {
        @include button($black, $black, true);
        &:hover {
            background-color:rgba(0, 0, 0, 0);
            border-color: darken($orange, 20%);
            color: darken($orange, 30%);
        }
    }
}

@media (max-width:900px) {
    
    .singup {
        flex-direction: column;
        justify-content: center;
    }

    .singup__text {
        margin-bottom: 35px;
    }
}

@media (max-width:500px) {

    .singup__text {
        text-align: center;
        font-size: px-to-rem(26px);

    }

    .singup__btns {
        flex-direction: column;
        justify-content: center;

        .btn {
            width: 280px;
        }
    }
}
