.about__wrap {
    background-color: $bg_gray_2;
    max-width: $mw_container;
    margin: 0 auto;
}

.about {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: 'find imageright'
                         'imageleft work' ;

    h1 {
        font-size: px-to-rem(56px);
        font-weight: 400;
        line-height: px-to-rem(66px);
    }

    p {
        font-size: px-to-rem(20px);
        line-height: px-to-rem(28px);
    }
}

.about__find {
    grid-area: find;
    justify-content: center;
    align-items: center;
    display: grid;

    div {
        max-width: 440px;
    }

    p {
        margin: 45px 0 52px 0;
    }
}

.about__image-right {
    grid-area: imageright;
}

.about__image-left {
    grid-area: imageleft;
}

.btn {
    @include button($orange,#fff, false);
    width: 200px;
}

.about__work {
    grid-area: work;
    justify-content: center;
    align-items: center;
    display: grid;

    div {
        max-width: 440px;
    }

    .btn {
        @include button($font-color: $orange, $border: true); 
        background: rgba(0, 0, 0, 0);
             
    }

    p {
        margin: 25px 0 35px 0;
        text-align: justify;
    }
}

.about__image {
    width: 100%;
    height: auto;
    object-fit: contain;
}

@media (max-width: 1000px) {

    .about {        
        grid-template-columns: 100%;
        grid-template-areas:    
                            'imageright'
                            'find'
                            'imageleft'
                            'work' ;
    
        .btn {
            width: 160px;
            font-size: px-to-rem(15px);
        }

    }



    .about__image {
        margin: 0 auto;
    }

    .about__work , .about__find {
        padding: 45px 20px;
    }
    
}

@media (max-width: 568px) {

    .about {
        
        h1 {
            font-size: px-to-rem(35px);
            font-weight: 400;
            line-height: px-to-rem(45px);
        }
    
        h2 {
            font-size: px-to-rem(26px);
            font-weight: 400;
        }
    
        p {
            font-size: px-to-rem(16px);
            line-height: px-to-rem(26px);
            margin: 25px 0 30px;
        }
    }
    
}