.posts__wrap {
    padding: 80px 0;
}
.posts {
}
.wrap {
}

.posts__block {
    display: grid;
    row-gap: 40px;
    column-gap: 30px;
    grid-template-areas: 
                'postbig postbig'
                'postleft postright';

}
.posts__block-big {
    grid-area: postbig;
    display: flex;
    background-color: $brown;
    color: $white;
    align-items: center;
}
.posts-left {
    grid-area: postleft;
}
.posts-righ {
    grid-area: postright;
}

.posts__block-big-text {
    margin-left: 70px;
    max-width: 490px;

    h3 {
        font-size: px-to-rem(48px);
        line-height: px-to-rem(56px);
        font-weight: 400;
        margin: 27px 0 60px 0;  
    }
    .posts__block-big-author {
        font-size: px-to-rem(14px);
    }

    .posts__block-big-btn {
        font-size: px-to-rem(18px);
        text-decoration: none;
        color: $white;
        &:hover {
            text-decoration: underline;
        }
    }
}

.posts__block-item {
    padding: 50px;

    h3 {
        font-size: px-to-rem(24px);
        line-height: px-to-rem(33px);
        margin-bottom: 20px;
    }

    p {
        font-size: px-to-rem(18px);
        line-height: px-to-rem(25px);
        margin-bottom: 20px;
    }

    a {
        text-decoration: none;
        font-size: px-to-rem(14px);
        color: $black;
        &:hover {
            text-decoration: underline;
        }
    }

    &:hover {
        box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05);
    }
}

@media (max-width:1024px) {

    .posts__block-big-text {
        margin-left: 40px;
        margin-right: 40px;
        max-width: 400px;

        h3 {
            font-size: px-to-rem(40px);
            line-height: px-to-rem(50px);
        }

    }

}


@media (max-width:800px) {

    .posts__block {
        row-gap: 20px;
        grid-template-areas: 
                    'postbig'
                    'postleft'
                    'postright';
    }

    .posts__block-big-text {
        margin-left: 20px;
        margin-right: 20px;
        max-width: 330px;

        h3 {
            font-size: px-to-rem(26px);
            line-height: px-to-rem(33px);
            margin: 15px 0 30px 0;  
        }

        .posts__block-big-author {
            font-size: px-to-rem(13px);
        }

        .posts__block-big-btn {
            font-size: px-to-rem(16px);
        } 

    }
  
}

@media (max-width:500px) {

    .posts__block-big {
        flex-direction: column;
    }

    .posts__block-big-img {
        width: 100%;

        img {
            width: 100%;
        }
    }

    .posts__block-big-text {
        margin: 30px;
        max-width: 380px;

        .posts__block-big-author {
            font-size: px-to-rem(12px);
        }

        .posts__block-big-btn {
            font-size: px-to-rem(14px);
        }
        
        h3 {
            font-size: px-to-rem(24px);
            line-height: px-to-rem(33px);
            margin: 15px 0 30px 0;  

        }

    }

    .posts__block-item {
        padding: 30px;

        p {
            font-size: px-to-rem(16px);
            line-height: px-to-rem(22px);
            margin-bottom: 20px;
        }
    }

    
  
}



